/*Home + SignIn + SignUp*/
import "./CSS/App.css";
import React, { useState, useEffect } from "react";
import Post from "./Post";
import { db, auth } from "./firebase";
import {
  Button,
  Snackbar,
  ThemeProvider,
  Grid,
  TextField
} from "@material-ui/core";
import theme from "./utils/theme";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import ImageUpload from "./ImageUpload";
import Avatar from "@material-ui/core/Avatar";
import Logo from "../src/images/logo.jpg";
import { Alert } from "@material-ui/lab";

/*Material Ui for Modal*/

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3)
  }
}));

export default function App() {
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);
  const [posts, setPosts] = useState([]);
  const [open, setOpen] = useState(false);

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [Confirm_password, setConfirm_password] = useState("");
  const [email, setEmail] = useState("");
  const [user, setUser] = useState(null);
  const [openSignIn, setOpenSignIn] = useState("");

  const [message, setMessage] = useState({
    open: false,
    type: "error", // or success
    text: ""
  });

  /*State of Being Login or Logout*/

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        console.log(authUser);
        setUser(authUser);
      } else {
        setUser(null);
      }
    });
    return () => {
      /*cleanup*/
      unsubscribe();
    };
  }, [user, username]);

  /*Posting Post*/

  useEffect(() => {
    db.collection("posts").onSnapshot((snapshot) => {
      setPosts(snapshot.docs.map((doc) => ({ id: doc.id, post: doc.data() })));
    });
  }, []);

  /*SignUp Registration*/

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setMessage({ ...message, open: false });
  };

  const showMessage = (type, text) => {
    setMessage({ ...message, open: true, type, text });
  };

  const signUp = (e) => {
    e.preventDefault();

    if (!email || !password || !Confirm_password) {
      showMessage("error", "Please fillout all fields!");
    } else if (password !== Confirm_password) {
      showMessage("error", "Password Does not Match!");
    } else {
      auth
        .createUserWithEmailAndPassword(email, password)
        .then((authUser) => {
          showMessage("success", "Successfuly Registered");
          return authUser.user.updateProfile({
            displayName: username
          });
        })

        .catch((error) => {
          showMessage("error", error.message);
        });

      setOpen(false);
      showMessage("");
    }
  };

  /*SignIn Authentication*/

  const signIn = (e) => {
    e.preventDefault();
    if (!email || !password) {
      showMessage("error", "Please fillout all fields!");
    } else {
      auth.signInWithEmailAndPassword(email, password).catch((error) => {
        showMessage("error", error.message);
      });
      setOpenSignIn(false);
    }
  };

  return (
    <div className="app">
      <ThemeProvider theme={theme}>
        <div className="appHeader">
          <img className="appHeaderImage" src={Logo} alt="" />
          <Avatar
            className="postAvatar"
            alt={username}
            src="/static/images/avatar/1.jpg"
          />
          {user ? (
            <Button
              variant="contained"
              color="secondary"
              onClick={() => auth.signOut()}
            >
              {" "}
              Sign out{" "}
            </Button>
          ) : (
            <div>
              <Grid container direction="row" spacing={1}>
                <Grid item>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => setOpenSignIn(true)}
                  >
                    {" "}
                    Sign In{" "}
                  </Button>
                </Grid>
                <Grid item>
                  <Button
                    variant="contained"
                    color="secondary"
                    onClick={() => setOpen(true)}
                  >
                    {" "}
                    Sign Up{" "}
                  </Button>
                </Grid>
              </Grid>
            </div>
          )}
        </div>

        {/*Call for ImageUpload*/}

        {user?.displayName ? (
          <ImageUpload username={user.displayName} />
        ) : (
          <center>
            <h2> You need to Sign In first! </h2>
          </center>
        )}

        {/*Modal Main Code for SignIn + SignUp popup Window*/}

        <Modal open={open} onClose={() => setOpen(false)}>
          <div style={modalStyle} className={classes.paper}>
            <center>
              <Grid container direction="column" spacing={2}>
                <img className="appHeaderImage1" src={Logo} alt="" />
                <Grid item>
                  <TextField
                    variant="outlined"
                    label="username"
                    type="text"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                  />
                </Grid>
                <Grid item>
                  <TextField
                    variant="outlined"
                    label="email"
                    type="text"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Grid>
                <Grid item>
                  <TextField
                    variant="outlined"
                    label="password"
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </Grid>
                <Grid item>
                  <TextField
                    variant="outlined"
                    label="Confirm_password"
                    type="password"
                    value={Confirm_password}
                    onChange={(e) => setConfirm_password(e.target.value)}
                  />
                </Grid>

                <Button
                  variant="contained"
                  color="primary"
                  type="submit"
                  onClick={signUp}
                >
                  Sign Up
                </Button>
              </Grid>
            </center>
          </div>
        </Modal>

        <Modal open={openSignIn} onClose={() => setOpenSignIn(false)}>
          <div style={modalStyle} className={classes.paper}>
            <center>
              <Grid container direction="column" spacing={2}>
                <img className="appHeaderImage1" src={Logo} alt="" />
                <Grid item>
                  <TextField
                    variant="outlined"
                    label="email"
                    type="text"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Grid>
                <Grid item>
                  <TextField
                    variant="outlined"
                    label="password"
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </Grid>
                <Grid item></Grid>

                <Button
                  variant="contained"
                  color="primary"
                  type="submit"
                  onClick={signIn}
                >
                  Sign In
                </Button>
              </Grid>
            </center>
          </div>
        </Modal>

        {/*Geting Data in Database for Posting*/}
        <div className="appPost">
          {posts.map(({ id, post }) => (
            <Post
              key={id}
              postId={id}
              user={user}
              username={post.username}
              caption={post.caption}
              imageUrl={post.imageUrl}
            />
          ))}
        </div>
      </ThemeProvider>
      <Snackbar
        open={message.open}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        autoHideDuration={6000}
        onClose={handleClose}
      >
        <Alert onClose={handleClose} severity={message.type}>
          {message.text}
        </Alert>
      </Snackbar>
    </div>
  );
}
